import React, { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import Main from '../components/section/Main';
import axios from 'axios';


const Live = () => {
    const [arrivalInfo, setArrivalInfo] = useState(null); // 도착 정보 상태

    const location = useLocation();
    const stationName = location.state && location.state.stationName;

    const fetchDataFromFlask = async (stationName) => {
        try {
            // stationName을 포함하는 JSON 데이터 생성
            const requestData = { stationName };

            // Flask 서버로 stationName을 전달하고 도착 정보를 받아옴
            const response = await axios.post(
                `http://flask:8082/subway_arrival`,
                requestData,
                {
                    headers: {
                        'Content-Type': 'application/json'
                    }
                }
            );

            // 받아온 도착 정보를 상태에 저장
            setArrivalInfo(response.data);
        } catch (error) {
            console.error('데이터 가져오기 실패:', error);
            console.log('에러 응답:', error.response); // 에러 응답 확인
        }
    };

    // 컴포넌트가 마운트될 때 데이터 가져오기
    useEffect(() => {
        fetchDataFromFlask();
    }, []);

    return (
        <Main
            title="상행하행"
            description="테스트 페이지"
        >
            {/* stationName이 존재하는 경우에만 아래 내용을 표시 */}
            {stationName && (
                <div>
                    <h2>실시간 도착 정보: {stationName}</h2>
                    {/* 실시간 도착 정보를 표시하는 컴포넌트나 로직을 추가할 수 있습니다. */}
                </div>
            )}
            {/* stationName이 존재하지 않는 경우에만 아래 내용을 표시 */}
            {!stationName && (
                <div>
                    <h1>실시간 도착정보가 없습니다.</h1>
                    <h4>지도에서 실시간 도착정보를 클릭해주세요.</h4>
                </div>
            )}
        </Main>
    );
};

export default Live;